.clearfix:after {visibility: hidden; display: block; font-size: 0; content: ''; clear: both; height: 0; }

html { -moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box;}

*, *:before, *:after {box-sizing: inherit}

.visuallyhidden:not(:focus):not(:active) { position: absolute; width: 1px; height: 1px; margin: -1px;border: 0;padding: 0;white-space: nowrap;clip-path: inset(100%);clip: rect(00 0 0);overflow: hidden;}

.wrapper {
  margin: 0 auto;
  max-width: 95%;
  width: 95%;
}

body {
  text-align: center;
  font-family: 'Bitter', serif;
  letter-spacing: 4px;
  color: #000;
  background-image: url("./assets/background.jpg");
  background-size: contain;
}

h1{
  font-family: 'Bitter', serif;
  font-size: 2.5em;
  letter-spacing: 5px;
  margin: 10px 0 10px;
  text-transform: uppercase;
  color: #000;
}
h2 {
  font-family: 'Bitter', serif;
  font-size: 1.8em;
  letter-spacing: 3.5px;
  margin: 10px 0 10px;
  text-transform: uppercase;
  color: #000;
}

ul {
  list-style-type: none;
  padding-left: 0;
}
a {
  text-decoration: none;
}
.fas {
  font-size: 25px;
}
.searchForm {
  max-width: 400px;
  display: flex;
  margin: 0 auto 10px;
}
.searchForm button {
  outline: none;
  margin-right: auto;
  background-color: darkcyan;
  border: none;
  padding: 10px 10px;
  border-radius: 10rem;
  cursor: pointer;
}
input {
  font-size: 1em;
  font-family: 'Bitter', serif;
  width: 85%;
  margin-left: auto;
  background-color: whitesmoke;
  padding: 10px 15px;
  border: 3px solid #d7dbdf;
  border-right: none;
  border-radius: 10rem;
  outline: none;
}
.mainNav li {
  width: 85%;
  margin-bottom: 1em;
  margin-left: auto;
  margin-right: auto;
  font-weight: bold;
}
.mainNav a {
  display: block;
  background: darkcyan;
  border-radius: 5px;
  padding: 10px;
  color: #fff;
}
.mainNav .active,
.mainNav a:hover,
.searchForm button:hover {
  background-color: goldenrod;
  transition: transform 1.3s ease-in-out;

}
.photoContainer ul {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  -ms-align-items: flex-start;
  align-items: flex-start;
}
.photoContainer ul li {
  width: 220px;
  height: 155px;
  border-radius: 10%;
  overflow: hidden;
  margin-bottom: 15px;
}
.photoContainer ul img {
  width: 100%;
  transform: scale(1.2);
  transition: transform 1.1s ease-in-out; 
}
.photoContainer li:hover img {
  transform: scale(0.9);
}
.photoContainer .not-found {
  width: 100%;
}

@media only screen and (min-width: 525px) {
  .mainNav li {
    width: 45%;
  }
}

@media only screen and (max-width: 767px) {
  body {
    padding-top: 2em;
  }
  .photoContainer li {
    margin: auto;
  }
}

@media only screen and (min-width: 768px) {
  body {
    padding-top: 4em;
  }
  input {
    width: 100%;
  }
  .container {
    max-width: 960px;
    margin: auto;
  }
  .mainNav ul {
    display: flex;
    justify-content: center;
  }
  .mainNav li {
    margin: 10px;
    width: 100px;
  }
}
